// pages / dashboard / facility / booking

// Modules
import React from "react";
import {connect} from 'react-redux';
import Helmet from 'react-helmet';
import Link from "gatsby-link";

// Styles
// import styles from '../../../style/preloader.module.css';

// Components
// import ConversationSidebar from '../../../components/dashboard/conversation/ConversationSidebar';
// import ChatMessage from '../../../components/dashboard/conversation/ChatMessage';


class Booking extends React.Component {

    constructor() {
        super();
        this.state = {
            eventName: '',
            timeBefore: '',
            timeAfter: ''
        }
    }

    componentDidMount() {
        let date = new Date();
        let d = date.getDate();
        let m = date.getMonth();
        let y = date.getFullYear();
        let obj = this;

        $('#calendar').fullCalendar({
            header: {
                left: 'prev,next today',
                center: 'title',
                right: 'month,agendaWeek,agendaDay'
            },
            selectable: true,
            // editable: true,
            // droppable: true,
            // drop: function() {
            //     // is the "remove after drop" checkbox checked?
            //     if ($('#drop-remove').is(':checked')) {
            //         // if so, remove the element from the "Draggable Events" list
            //         $(this).remove();
            //     }
            // },
            events: [
                {
                    title: 'Swimming Pool',
                    start: new Date(y, m, 1)
                },
                {
                    title: 'Multipurpose Hall',
                    start: new Date(y, m, d-5),
                    end: new Date(y, m, d-2)
                },
                // {
                //     id: 999,
                //     title: 'Repeating Event',
                //     start: new Date(y, m, d-3, 16, 0),
                //     allDay: false
                // },
                // {
                //     id: 999,
                //     title: 'Repeating Event',
                //     start: new Date(y, m, d+4, 16, 0),
                //     allDay: false
                // },
                {
                    title: 'Multipurpose Hall',
                    start: new Date(y, m, d, 10, 30),
                    allDay: false
                },
                {
                    title: 'Garden',
                    start: new Date(y, m, d, 12, 0),
                    end: new Date(y, m, d, 14, 0),
                    allDay: false
                },
                {
                    title: 'Badminton Court',
                    start: new Date(y, m, d+1, 19, 0),
                    end: new Date(y, m, d+1, 20, 0),
                    allDay: false
                },
                {
                    title: 'Multipurpose Hall',
                    start: new Date(y, m, 28, 19, 0),
                    end: new Date(y, m, 28, 22, 30),
                    allDay: false
                }
            ],
            eventClick: function(calEvent, jsEvent, view) {
                // alert('Event: ' + calEvent.title);
                // alert('Coordinates: ' + jsEvent.pageX + ',' + jsEvent.pageY);
                // alert('View: ' + view.name);
                // $(this).css('border-color', 'red');


                // set selected event
                obj.setEventDetail({
                    eventName: calEvent.title,
                    timeBefore: moment(calEvent.start._d).format('h:mm a - D/M/YY'),
                    timeAfter: moment(calEvent.end._d).format('h:mm a - D/M/YY')
                });

                $('#modalEditEvent').modal('show');
            },
            dayClick: function(date, jsEvent, view) {
                // alert('Clicked on: ' + date.format());
                // alert('Coordinates: ' + jsEvent.pageX + ',' + jsEvent.pageY);
                // alert('Current view: ' + view.name);
                // $(this).css('background-color', 'red');

                $('#modalAddEvent').modal('show');
            }

        });



        $(".select-facility").select2({
            placeholder: "Select facility",
            allowClear: true,
            width: '100%'
        });

        $('.clockpicker').clockpicker({
            placement: 'bottom',
            'default': 'now',
            donetext: 'Done'
        });
    }

    bookFacility() {
        let date = new Date();
        let d = date.getDate();
        let m = date.getMonth();
        let y = date.getFullYear();

        // add event
        $('#calendar').fullCalendar('renderEvent', {
            title: 'Badminton Court (Pending)',
            start: new Date(y, m, d+1, 20, 0),
            end: new Date(y, m, d+1, 21, 0),
            allDay: false
        });

        $('#modalAddEvent').modal('toggle');
    }

    setEventDetail(arg) {
        this.setState({eventName: arg.eventName});
        this.setState({timeBefore: arg.timeBefore});
        this.setState({timeAfter: arg.timeAfter});
    }

    render() {
        if (this.props.user.loggedIn) {

            return (
              <span>

                  {/* Start of Helmet */}
                  <Helmet title="Facility - booking" meta={[
                      {
                          name: 'description',
                          content: 'Facility - booking'
                      }, {
                          name: 'keywords',
                          content: 'Facility'
                      }
                  ]}/>
                  {/* End of Helmet */}


                  {/* Start of Heading */}
                  <div className="row wrapper border-bottom white-bg page-heading">
                      <div className="col-lg-8">
                          <h2>Facility Booking</h2>
                          <ol className="breadcrumb">
                              <li>
                                  <Link to="/dashboard">Home</Link>
                              </li>
                              <li className="active">
                                  <strong>Facility Booking Calendar</strong>
                              </li>
                          </ol>
                      </div>
                  </div>
                  {/* End of Heading */}



                  {/* Start of Modal */}
                  <div className="modal inmodal" id="modalEditEvent" tabIndex="-1" role="dialog"  aria-hidden="true">
                      <div className="modal-dialog">
                          <div className="modal-content animated fadeIn">
                              <div className="modal-header">
                                  <button type="button" className="close" data-dismiss="modal"><span aria-hidden="true">&times;</span><span className="sr-only">Close</span></button>
                                  <i className="fa fa-calendar modal-icon"></i>
                                  <h4 className="modal-title"> {this.state.eventName} </h4>
                                  <small>Badminton is a racquet sport played using racquets to hit a shuttlecock across a net.</small>
                              </div>
                              <div className="modal-body">
                                  <p>
                                      <strong>Booking Time: </strong>
                                      <span>
                                          <strong>({this.state.timeBefore}</strong> until <strong>{this.state.timeAfter})</strong>
                                      </span>
                                  </p>
                              </div>
                              <div className="modal-footer">
                                  <button type="button" className="btn btn-white" data-dismiss="modal">Close</button>
                                  <button type="button" className="btn btn-primary">Save changes</button>
                              </div>
                          </div>
                      </div>
                  </div>

                  <div className="modal inmodal" id="modalAddEvent" tabIndex="-1" role="dialog"  aria-hidden="true">
                      <div className="modal-dialog">
                          <div className="modal-content animated fadeIn">
                              <div className="modal-header">
                                  <button type="button" className="close" data-dismiss="modal"><span aria-hidden="true">&times;</span><span className="sr-only">Close</span></button>
                                  <i className="fa fa-calendar modal-icon"></i>
                                  <h4 className="modal-title">Book a Facility</h4>
                                  <small>Please select the available faciliy and the time you want to book</small>
                              </div>
                              <div className="modal-body">
                                  <form className="form-horizontal">
                                      <div className="form-group">
                                          <label htmlFor="fieldState" className="col-lg-2 control-label">Facility *</label>
                                          <div className="col-lg-10">
                                              <select className="select-facility form-control" id="fieldState" defaultValue="" required >
                                                  <option></option>
                                                  <option value="Wp Kuala Lumpur">Multipurpose Hall</option>
                                                  <option value="Wp Putrajaya">Movie Hall</option>
                                                  <option value="Wp Labuan">Computer Room</option>
                                                  <option value="Johor">Badminton Court</option>
                                                  <option value="Kedah">Swimming Pool</option>
                                              </select>
                                          </div>
                                      </div>

                                      <div className="form-group">
                                          <label className="col-lg-2 control-label" htmlFor="lastName">Starting time *</label>
                                          <div className="col-lg-10">

                                              <div className="input-group clockpicker" data-autoclose="true">
                                                  <input type="text" className="form-control" placeholder="Please choose allowed start time" required />
                                                  <span className="input-group-addon">
                                                      <span className="fa fa-clock-o"></span>
                                                  </span>
                                              </div>
                                          </div>
                                      </div>
                                      <div className="form-group">
                                          <label className="col-lg-2 control-label" htmlFor="lastName">Ending time *</label>
                                          <div className="col-lg-10">

                                              <div className="input-group clockpicker" data-autoclose="true">
                                                  <input type="text" className="form-control" placeholder="Please choose allowed end time" required />
                                                  <span className="input-group-addon">
                                                      <span className="fa fa-clock-o"></span>
                                                  </span>
                                              </div>
                                          </div>
                                      </div>

                                      <small>(*) fields are required to be filled in</small>
                                  </form>
                              </div>
                              <div className="modal-footer">
                                  <button type="button" className="btn btn-white" data-dismiss="modal">Close</button>
                                  <button type="button" className="btn btn-primary" onClick={this.bookFacility.bind(this)} >Book Facility</button>
                              </div>
                          </div>
                      </div>
                  </div>
                  {/* End of Modal */}


                  {/* Start of Content */}
                  <div className="wrapper wrapper-content">
                      <div className="row animated fadeInDown">
                          <div className="col-lg-3">
                              <div className="ibox float-e-margins">
                                  <div className="ibox-content">
                                      <h2>How to Book a Facility</h2>
                                      To book a facility, just click the date column box on the calendar, then set the booking hours you desired. Finally,
                                      wait for admin of the property to approve your booking, then you able to use that particular facility at given date &amp; time.
                                      <br/><br/>
                                      <h2>Edit Approved Booking</h2>
                                      To edit your approved booking, just click the booking stripe, then edit the duration of booking or the facility according to your choice.
                                      Then, again awaiting for the admin of the property to approve your booking.
                                  </div>
                              </div>
                          </div>
                          <div className="col-lg-9">
                              <div className="ibox float-e-margins">
                                  <div className="ibox-title">
                                      <h5>Calendar Overview for Booking Facility</h5>
                                      <div className="ibox-tools">
                                          <a className="collapse-link">
                                              <i className="fa fa-chevron-up"></i>
                                          </a>
                                          <a className="dropdown-toggle" data-toggle="dropdown" href="#">
                                              <i className="fa fa-wrench"></i>
                                          </a>
                                          <ul className="dropdown-menu dropdown-user">
                                              <li><a href="#">Config option 1</a>
                                              </li>
                                              <li><a href="#">Config option 2</a>
                                              </li>
                                          </ul>
                                          <a className="close-link">
                                              <i className="fa fa-times"></i>
                                          </a>
                                      </div>
                                  </div>
                                  <div className="ibox-content">
                                      <div id="calendar"></div>
                                  </div>
                              </div>
                          </div>
                      </div>
                  </div>
                  {/* End of Content */}

              </span>
            );
        } else {
            return null;
        }
    }
};

const mapStateToProps = (state) => {
    return {user: state.users.user};
}

const mapDispatchToProps = (dispatch) => {
    return {};
}

export default connect(mapStateToProps, mapDispatchToProps())(Booking);



